@charset "utf-8";
@import "../includes/zindex";
.scr-navi {
  .swiper-wrapper {
    .swiper-slide {
      a {
        font-size: 4vw;
      }
    }
  }
}
.line {
  width: 90.4%;
  height: .8vw;
  margin: 0 auto;
  background: #000;
}
.h-bg {
  background: #f8f8f8;
}
.c-bg {
  background: #f8f8f8;
  padding-top: 6.666vw;
  .order {
    margin-top: 0;
  }
}
.order.check {
  padding: 0 0 4.8vw;
  .btn-order {
    margin: 9.6vw auto 0;
  }
}
.order.check>div {
  padding: 0 4.8vw;
}
.order.check> .gift {
  padding: 0;
}
.order.check .gift.on + .ad-info {
  margin-top: 9.6vw;
}
.order {
  margin-top: 6.666vw;
  padding: 0 0 9.6vw;
  .order-prod {
    padding: 0 4.8vw;
    .top {
      padding-top: 5.333vw;
      overflow: hidden;
      border-top: .8vw #000 solid;
      h3 {
        font-size: 3.733vw;
        color: #1f1f1f;
        float: left;
      }
      a {
        font-size: 0;
        text-indent: -999px;
        display: block;
        width: 3.066vw;
        height: 1.733vw;
        background: url('/_ui/img/m/icn/icn-arrow04.png') no-repeat;
        background-size: 100%;
        float: right;
        margin-top: 1.333vw;
        &.on {
          -webkit-transform: rotate(-180deg);
          -moz-transform: rotate(-180deg);
          -o-transform: rotate(-180deg);
          -ms-transform: rotate(-180deg);
          transform: rotate(-180deg);
        }
      }
    }
    ul {
      margin: 6.666vw 0 5.6vw;
      li {
        overflow: hidden;
        margin-top: 2.133vw;
        padding-bottom: 6.933vw;
        display: none;
        &:first-child {
          margin-top: 0;
          display: block;
        }
        .prod {
          position: relative;
          width: 25.333vw;
          float: left;
          a {
            &::before {
              content: '';
              display: block;
              position: absolute;
              top: 0;
              right: 0;
              bottom: 0;
              left: 0;
              background: #000;
              opacity: .03;
              z-index: 1;
            }
          }
          img {
            width: 100%;
          }
        }
        .t1 {
          font-family: Helvetica, sans-serif;
          margin: 1.333vw 0 2.666vw;
          overflow: hidden;
          .brand {
            float: left;
            font-weight: 700;
            font-size: 2.666vw;
            color: #1f1f1f;
          }
          .brand + span {
            font-size: 2.4vw;
            color: #bbb;
            float: right;
          }
        }
        .t2 {
          font-size: 3.733vw;
          margin-bottom: 5.6vw;
          color: #1f1f1f;
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
          text-align: left;
          line-height: 1.2;
          white-space: normal;
          word-wrap: break-word;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          box-sizing: border-box;
          word-break: normal;
          -moz-line-clamp: 2;
          -moz-box-orient: vertical;
          display: -moz-box;
          clear: both;
          a {
            font-family: "Noto Sans KR";
            color: #1f1f1f;
          }
        }
        .info {
          width: 61.066vw;
          padding-left: 4vw;
          float: left;
        }
        .option {
          color: #1f1f1f;
          font-size: 3.466vw;
          margin-bottom: .666vw;
        }
        .amount {
          color: #1f1f1f;
          font-size: 3.466vw;
          margin-bottom: .666vw;
        }
        .btm {
          font-size: 3.466vw;
          .point {
            margin-bottom: .666vw;
          }
          .price-family {
            margin: .666vw 0;
            color: #f23d4e;
          }
        }
        .delivery-info {
          overflow: hidden;
          width: 100%;
          margin-top: 5.066vw;
          font-size: 3.466vw;
          color: #898989;
          dl {
            overflow: hidden;
            line-height: 5.066vw;
            width: 62vw;
          }
          dt,
          dd {
            float: left;
          }
          dt {
            width: 17.866vw;
          }
          dd {
            width: 43.2vw;
          }
        }
        .price {
          color: #1f1f1f;
        }
        .discount {
          color: #9ba4b0;
          text-decoration: line-through;
          margin-right: 2.666vw;
        }
      }
      &.on li {
        display: block;
      }
    }
    .setItem {
      overflow: hidden;
      .info {
        padding-bottom: 0;
      }
      .btn-set-option {
        float: left;
        margin: 4vw 0;
        a {
          display: block;
          font-family: "Noto Sans KR";
          font-size: 3.2vw;
          color: #1f1f1f;
          &::after {
            content: '';
            display: inline-block;
            width: 1.733vw;
            height: 1.2vw;
            margin-left: 1.6vw;
            background: url('/_ui/img/m/icn/icn-arrow05.png') no-repeat;
            background-size: contain;
            vertical-align: middle;
          }
          &.on {
            &::after {
              transform: rotate(-180deg);
              -webkit-transform: rotate(-180deg);
              -ms-transform: rotate(-180deg);
              -moz-transform: rotate(-180deg);
            }
          }
        }
      }
      .set-option-list {
        display: none;
        clear: both;
        padding-top: 6.933vw;
        border-top: .266vw solid #f1f1f1;
        .list {
          overflow: hidden;
          padding-bottom: 3.466vw;
          .prod-add {
            font-size: 3.466vw;
            color: #1f1f1f;
          }
        }
      }
      .set-option-list.on {
        display: block;
      }
    }
    .setItem ~ .delivery-info {
      margin-top: 1.333vw;
      margin-left: 29.3vw;
    }
  }
  .packing {
    .top {
      border-top: 0.266vw solid #f1f1f1;
      padding-top: 5.333vw;
      h3 {
        font-size: 3.733vw;
        margin-bottom: 4vw;
      }
      p {
        font-size: 3.466vw;
        color: #898989;
      }
    }
    .cont {
      margin: 12vw 0;
      .tit {
        float: left;
        padding-right: 10.4vw;
        font-size: 3.733vw;
        line-height: 5.333vw;
        color: #898989;
      }
      .radio {
        position: relative;
        display: inline-block;
        padding-right: 5.333vw;
        i {
          float: left;
          position: absolute;
          width: 5.333vw;
          height: 5.333vw;
          background: url('/_ui/img/m/icn/icn-radio-off.png') no-repeat;
          background-size: 100%;
        }
        input[type="radio"] {
          display: none;
          &:checked + i {
            background: url('/_ui/img/m/icn/icn-radio-on.png') no-repeat;
            background-size: 100%;
          }
        }
        label {
          float: left;
          font-size: 3.466vw;
          color: #1f1f1f;
          padding-left: 7.733vw;
          position: relative;
          line-height: 5.333vw;
        }
      }
    }
  }
  // .gift.on + .d-info .d-top {
  //   display: none;
  // }
  .gift.on {
    margin-bottom: 0;
    .top {
      border-bottom: 0;
    }
    .gift-choice {
      display: block;
    }
    // .d-gift {
    //   display: block;
    // }
  }
  .gift {
    margin-bottom: 9.6vw;
    .d-top {
      width: 90.4vw;
      margin: 9.6vw auto;
    }
    .top {
      padding: 5.333vw 0;
      margin: 0 auto;
      overflow: hidden;
      width: 90.4vw;
      border-top: .266vw #f1f1f1 solid;
      border-bottom: .266vw #f1f1f1 solid;
      h3 {
        font-size: 3.733vw;
        color: #1f1f1f;
        float: left;
      }
      .more {
        font-size: 0;
        text-indent: -999px;
        display: block;
        width: 3.066vw;
        height: 1.733vw;
        background: url('/_ui/img/m/icn/icn-arrow04.png') no-repeat;
        background-size: 100%;
        float: right;
        margin-top: 1.333vw;
      }
      .more.on {
        -webkit-transform: rotate(-180deg);
        -moz-transform: rotate(-180deg);
        -o-transform: rotate(-180deg);
        -ms-transform: rotate(-180deg);
        transform: rotate(-180deg);
      }
    }
    .gift-noti {
      padding: 0 4.8vw;
      font-size: 3.466vw;
      color: #898989;
      p {
        margin-top: 1.066vw;
      }
    }
    .gift-choice {
      display: none;
      background: #f7f7f7;
      padding: 8.8vw 0;
      li .tit {
        font-size: 3.466vw;
        color: #1f1f1f;
      }
      .list {
        margin-top: 4vw;
        li {
          background: #fff;
          overflow: hidden;
          margin-top: 2.933vw;
          padding: 0 2vw;
          cursor: pointer;
          &:first-child {
            margin-top: 0;
          }
          a {
            display: block;
          }
          &.select {
            background: #fff url('/_ui/img/m/icn/icn-check03.png') no-repeat 96% 50%;
            background-size: 2.933vw 2.133vw;
          }
        }
        .prod {
          display: inline-flex;
          display: -webkit-inline-flex;
          display: -ms-inline-flex;
          justify-content: center;
          -webkit-justify-content: center;
          align-items: center;
          -webkit-align-content: center;
          width: 19.733vw;
          min-height: 16vw;
          margin: 1.066vw 4vw 1.066vw 0;
          vertical-align: middle;
          img {
            width: 100%;
            font-size: 2.133vw;
          }
        }
        .info {
          display: inline-block;
          max-width: 54.666vw;
          vertical-align: middle;
          .t1 {
            color: #1f1f1f;
            font-size: 2.666vw;
            font-weight: 700;
          }
          .t2 {
            color: #898989;
            font-size: 2.933vw;
            margin-top: 1.066vw;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            letter-spacing: -.5px;
          }
        }
      }
    }
    .gift-choice>li {
      padding: 0 4.8vw;
      margin-top: 9.333vw;
      &:first-child {
        margin-top: 0;
      }
    }
    .d-gift {
      display: none;
    }
  }
  .d-top {
    padding: 6.666vw 0;
    overflow: hidden;
    border-top: .8vw #000 solid;
    h3 {
      font-size: 4vw;
      color: #1f1f1f;
      float: left;
      font-weight: 700;
    }
    div {
      float: right;
      a {
        color: #1f1f1f;
        font-size: 3.733vw;
        margin-left: 8.666vw;
      }
    }
  }
  .btn-order {
    display: block;
    height: 13.6vw;
    background: #f23d4e;
    color: #fff;
    font-size: 4vw;
    font-weight: 700;
    text-align: center;
    line-height: 13.6vw;
    margin: 9.6vw auto;
    width: 90.4vw;
  }
  .d-notice {
    padding: 0 4.8vw;
    h3 {
      color: #1f1f1f;
      font-size: 3.733vw;
      margin-bottom: 4.8vw;
      font-weight: 700;
    }
    ul {
      margin-bottom: 8.533vw;
      padding-left: 4.8vw;
      li {
        list-style: disc;
        font-size: 3.466vw;
        color: #aaa;
        margin-top: 2.4vw;
        line-height: 4.266vw;
        &:first-child {
          margin-top: 0;
        }
        .color {
          color: #1f1f1f;
        }
      }
    }
  }
  .ad-info {
    margin-bottom: 7.466vw;
    .d-top {
      padding: 6.666vw 0 4vw;
    }
    .d-cont {
      border: .266vw #f1f1f1 solid;
      margin-bottom: 2.133vw;
      padding: 5.333vw;
      line-height: 5.333vw;
      .name {
        font-size: 4vw;
        font-weight: 700;
        color: #1f1f1f;
        margin-bottom: 3.6vw;
      }
      .address {
        font-size: 3.733vw;
        color: #898989;
        .d-txt {
          border-top: .266vw #f1f1f1 solid;
          padding-top: 5.333vw;
          margin-top: 5.333vw;
        }
        p + p {
          margin-top: 2.666vw;
        }
      }
      .pickup-product {
        overflow: hidden;
        margin-top: 8.666vw;
        .prod {
          float: left;
          width: 12.8vw;
          height: 15.733vw;
          margin-right: 2.66vw;
          img {
            width: 100%;
          }
        }
        .info {
          float: left;
          width: 79%;
          .t1 {
            overflow: hidden;
            color: #1f1f1f;
            font-family: Helvetica, sans-serif;
            font-size: 2.666vw;
            font-weight: 700;
            line-height: 1;
            .brand {
              float: left;
              font-weight: 700;
              font-size: 2.666vw;
              color: #1f1f1f;
            }
            .code {
              float: right;
              font-size: 2.4vw;
              color: #bbb;
            }
          }
          .t2 {
            color: #1f1f1f;
            font-size: 3.4666vw;
            margin-top: 2vw;
            line-height: 1;
          }
          .btm {
            margin-top: 4vw;
            font-size: 3.2vw;
            line-height: 1;
            .discount {
              color: #9ba4b0;
              text-decoration: line-through;
              margin-right: 2.666vw;
            }
            .price {
              color: #1f1f1f;
            }
          }
        }
      }
      .pickup {
        .t1 {
          font-size: 4vw;
          color: #1f1f1f;
          margin-bottom: 2vw;
        }
        .t1 + dd {
          color: #898989;
          font-size: 3.466vw;
        }
        dd + dt {
          font-size: 3.466vw;
          color: #898989;
          margin: 6.666vw 0 2vw;
        }
        dd + dt + dd {
          color: #1f1f1f;
          font-size: 3.733vw;
        }
      }
    }
  }
  .d-discount {
    margin-bottom: 9.6vw;
    .top {
      overflow: hidden;
      line-height: 6.4vw;
      h4 {
        float: left;
        font-size: 3.733vw;
        color: #1f1f1f;
        i {
          color: #f23d4e;
        }
      }
    }
    .disc {
      color: #aaa;
      font-size: 3.2vw;
      margin-top: 3.333vw;
      line-height: 4.533vw;
      letter-spacing: -.5px;
    }
    .tg-cont {
      input:disabled {
        background: none;
      }
      .btn {
        float: right;
        display: block;
        box-sizing: border-box;
        border: .266vw #f1f1f1 solid;
        height: 8.266vw;
        width: 18.666vw;
        font-size: 3.733vw;
        line-height: 8.266vw;
        text-align: center;
        color: #1f1f1f;
        &.disabled {
          color: #bbb;
        }
      }
    }
    .coupon {
      margin-bottom: 8vw;
      .tg-cont {
        margin-top: 5.333vw;
        li {
          border: .266vw #f1f1f1 solid;
          margin-top: 1.066vw;
          font-size: 3.466vw;
          height: 12.533vw;
          line-height: 12.533vw;
          overflow: hidden;
          &:first-child {
            margin-top: 0;
          }
          .tit {
            color: #1f1f1f;
            float: left;
            text-align: center;
            width: 17.6vw;
          }
          select {
            color: #898989;
            float: left;
            width: 69.066vw;
            padding: 0 1.6vw;
            height: 12.533vw;
            font-size: 3.466vw;
            background: #fff url('/_ui/img/m/icn/icn-arrow05.png') no-repeat 100% 50%;
            background-size: 2vw 1.2vw;
          }
        }
        .add {
          margin-top: 9.333vw;
          font-size: 3.733vw;
          line-height: 8.266vw;
          color: #1f1f1f;
          overflow: hidden;
          position: relative;
          input {
            float: left;
            width: 66.4vw;
            height: 8.266vw;
            font-size: 3.733vw;
            border-bottom: .266vw #f1f1f1 solid;
          }
          input + span {
            display: none;
            position: absolute;
            right: 24.533vw;
            top: 0;
            color: #ccc;
            font-size: 3.733vw;
            line-height: 8.533vw;
          }
        }
        .add-coupon {
          height: 12.533vw;
          border: .266vw #f1f1f1 solid;
          line-height: 12.533vw;
          margin-top: 2.933vw;
          overflow: hidden;
          span {
            color: #1f1f1f;
            font-size: 3.733vw;
            margin-left: 2.266vw;
            font-weight: 700;
          }
          .btn-del {
            display: block;
            float: right;
            background: url('/_ui/img/m/icn/icn-close2.png') no-repeat;
            background-size: 100%;
            width: 3.466vw;
            height: 3.466vw;
            margin-right: 4.266vw;
            font-size: 0;
            text-indent: -999px;
            margin-top: 4.533vw;
          }
        }
      }
    }
    .point {
      margin-bottom: 8vw;
      .tg-cont {
        margin-top: 7.733vw;
        overflow: hidden;
        line-height: 8.266vw;
        .input {
          float: left;
          overflow: hidden;
          width: 66.4vw;
          height: 8.266vw;
          font-size: 3.733vw;
          border-bottom: .266vw #f1f1f1 solid;
          span {
            float: right;
            color: #898989;
            &::before {
              content: 'P ';
              color: #1f1f1f;
            }
          }
          input {
            float: right;
            max-width: 20vw;
            // width: 43.733vw;
            height: 8vw;
            line-height: 7vw;
            font-size: 3.733vw;
            text-align: right;
          }
        }
      }
    }
    .staff {
      margin-bottom: 8vw;
      .tg-cont {
        margin-top: 7.733vw;
        overflow: hidden;
        div {
          width: 66.4vw;
          height: 8.266vw;
          line-height: 8.266vw;
          border-bottom: .266vw #f1f1f1 solid;
          float: left;
          box-sizing: border-box;
          overflow: hidden;
          &::after {
            content: '';
            display: block;
            font-size: 3.733vw;
            color: #1f1f1f;
            float: right;
          }
          input {
            text-align: right;
            width: 61.866vw;
            height: 8vw;
            font-size: 3.733vw;
            float: left;
            display: block;
          }
        }
      }
    }
    .deposit {
      margin-bottom: 9.733vw;
      .top {
        span {
          color: #898989;
          float: right;
          font-size: 3.733vw;
          margin-right: 4vw;
        }
      }
      .tg-cont {
        margin-top: 7.733vw;
        overflow: hidden;
        line-height: 8.266vw;
        .input {
          float: left;
          overflow: hidden;
          width: 66.4vw;
          height: 8.266vw;
          font-size: 3.733vw;
          border-bottom: .266vw #f1f1f1 solid;
          span {
            float: right;
            color: #898989;
            &::before {
              content: ' ';
              color: #1f1f1f;
            }
          }
          input {
            float: right;
            // width: 37.866vw;
            max-width: 20vw;
            height: 8vw;
            line-height: 7vw;
            font-size: 3.733vw;
            text-align: right;
          }
        }
      }
    }
  }
  .total-price {
    background: #f7f7f7;
    dl {
      padding: 9.6vw 0;
      font-size: 3.466vw;
      color: #1f1f1f;
      overflow: hidden;
      dt {
        float: left;
        width: 50%;
        height: 7.333vw;
        line-height: 7.333vw;
        small {
          display: block;
          margin-top: -2.66vw;
          color: #898989;
          font-size: 2.933vw;
          height: 2.666vw;
        }
      }
      dd {
        float: left;
        width: 50%;
        text-align: right;
        height: 7.333vw;
        line-height: 7.333vw;
      }
      .total {
        margin-top: 6vw;
        font-size: 4.533vw;
        line-height: 4.666vw;
        height: 4.666vw;
        color: #1f1f1f;
        font-weight: 700;
        i {
          color: #f23d4e;
          font-weight: 700;
        }
      }
    }
    .type2 {
      height: 11.866vw;
    }
  }
  .payment + .payment {
    border-top: .266vw #f1f1f1 solid;
    &.use-deposit {
      padding-bottom: 9.6vw;
    }
  }
  .payment {
    .d-top {
      border-top: 0;
    }
    .cont {
      h3 {
        margin: 9.333vw 0 6.666vw;
        font-size: 3.466vw;
        font-weight: 700;
        color: #1f1f1f;
      }
      .pay-tab {
        overflow: hidden;
        li {
          float: left;
          width: 20.8vw;
          height: 20.8vw;
          box-sizing: border-box;
          border: .266vw #f1f1f1 solid;
